.floating-container {
  background-image: url(data:image/svg+xml;base64,);
  height: 100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 0px;
}

.signup-form {
  background: rgb(175, 121, 121);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  width: 360px;
  height: 540px;
  background-color: rgb(250, 250, 250);
  box-sizing: border-box;
  border-radius: 7px;
  box-shadow: rgb(0 0 0 / 16%) 3px 6px 6px;
}

.details {
  margin-top: 40px;
  margin-bottom: 40px;
}

.for-alignment {
  margin-top: 10px;
  color: rgb(68, 68, 68);
  width: 100%;
  background-color: transparent;
  outline: none;
  outline-offset: 0px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1.2px solid rgb(230, 230, 230);
  padding-bottom: 5px;
  font-size: 1rem;
}

.forgot-password {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.forgot-password:hover {
  text-decoration: underline rgb(112, 163, 183);
}

.signup-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 10px;
  margin-top: 15px;
  margin-bottom: 20px;
  background-color: bisque;
  border-radius: 5px;
}

.signup-button:hover {
  background-color: rgb(216, 125, 125);
  border-radius: 5px;
}

.instruction {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 340px;
}

.instruction:hover {
  text-decoration: underline rgb(112, 163, 183);
}

.graphic {
  background-size: 54.5px;
  margin: auto 5px 15px;
  height: 55px;
  box-sizing: border-box;
  background-image: url(data:image/svg+xml;base64,);
}
