.navbar-style-for-contact {
    border-bottom: 3px solid rgba(221, 226, 227, 0.2);
}

.designer-name {
  text-align: center;
  margin-top: 10px;
  padding-top: 0;
  margin-bottom: 30px;
  color: #666;
  font-style: normal;
  font-family: Muli, Helvetica Neue, sans-serif;
  font-size: 2.441rem;
  font-style: bold;
  font-weight: 900;
  margin: auto;
  padding-top: 1em;
  padding-bottom: 0.563em;
  width: 93%;
}

.floating-container {
  background-image: url(data:image/svg+xml;base64,);
  height: 100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 95px 0px;
}

.floating-card {
  display: flex;
  width: 400px;
  height: 590px;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-radius: 7px;
  box-shadow: rgb(0 0 0 / 16%) 3px 6px 6px;
}

.information {
  padding-bottom: 20px;
  padding-left: 50px;
  padding-right: 50px;
  height: 525px;
  display: flex;
  flex-direction: column;
}

.photopraph-div {
  display: flex;
  justify-content: center;
}

.photograph-alignment {
  margin-top: 60px;
  margin-bottom: 10px;
  border-radius: 50%;
  height: 174px;
  width: 174px;
  border: 2px solid #dde2e3;
}

.photograph {
  border: 6px solid #fff;
  border-radius: 50%;
  vertical-align: middle;
}

.picture {
  border-radius: 50%;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}

.about {
  line-height: 1.875rem;
  margin-bottom: 1.3em;
}

.email {
  margin-top: auto;
  margin-bottom: 1.3em;
}

.email-hyperlink:hover {
  text-decoration: underline rgb(112, 163, 183);
  color: rgb(112, 163, 183);
}

.graphic {
  background-size: 54.5px;
  margin: auto 5px 15px;
  height: 55px;
  box-sizing: border-box;
  background-image: url(data:image/svg+xml;base64,);
}
